.editor {
    margin: 10px;
    padding: 10px;
    resize: none;
    overflow: auto;
    border: none;
    flex: 1;
    font-family: "Sono", sans-serif;
    font-size: 1.5rem;
    background-color: var(--section-background);
    caret-color: var(--editor-green);
}

.editor:focus {
    outline: 5px solid var(--editor-green);
    border-radius: 3px;
}
